import 'whatwg-fetch';
import {Controller} from './controller';
import EventEmitter from './eventEmitter';

let Model = Object.create(EventEmitter);

//Model,
Model = {
    records: {},
    //model
    created() {
        this.records = {};  //model,records,recordsmodel
    },
    extend(obj = {}) {
        let extended = obj.extended;
        for (let key in obj) {
            this[key] = obj[key];
        }
        if (extended) extended.call(this);
    },
    include(obj = {}) {
        let included = obj.included;
        for (let key in obj) {
            this.prototype[key] = obj[key];
        }
        if (included) included.call(this);
    },
    prototype: {
        init() {

        }
    },
    create() {
        let object = Object.create(this);   //Model,init
        object.parent = this;               //.parent = Model

        object.prototype = object.fn = Object.create(this.prototype);

        object.created();

        return object;
    },
    init() {
        let instance = Object.create(this.prototype);
        instance.parent = this;             //.parent = 
        instance.init.apply(instance, arguments);   //Model.prototype.init();
        return instance;
    }
}


//ajax  
Model.include({
    post(url = '', obj = {}) {
        return new Promise((resolve, reject) => {
            fetch(url, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(obj)
            }).then((data) => {
                // reject
                resolve(data.json());
            })
        })
    },
    get(url = '') {
        return new Promise((resolve, reject) => {
            fetch(url)
                .then((data) => {
                    //
                    resolve(data.json());
                })
        })
    }
});

//hash
Model.include({
    getHash() {
        return window.location.hash.slice(2);
    }
});

//model
Model.include({
    pageInit() {

    }
});

//Model
Model.include({
    newRecord: true,
    create() {
        this.newRecord = false;
        //.records[this.name] = this;
        this.parent.records[this.name] = this.dup();
    },
    destory() {
        delete this.parent.records[this.name];
    },
    update() {
        this.parent.records[this.name] = this.dup();
    },
    dup() {
        return Object.assign({}, this);
    },
    save() {
        this.newRecord ? this.create() : this.update();
    }
});

Model.extend({
    find(name = '') {
        return this.records[name] || console.log('Unkonwn record');
    }
});


//localstorage
Model.include({
    setLocItem(key = '', value) {
        let itemValue,
            type = typeof value;
        if (type === 'string' || type === 'number') {
            itemValue = value;
        } else if (Object.prototype.toString.call(value) === '[object Object]') {
            itemValue = JSON.stringify(value);
        } else {
            itemValue = undefined;
        }
        localStorage.setItem(key, itemValue);
    },
    getLocItem(key = '') {
        return localStorage.getItem(key);
    },
    removeLocItem(key = '') {
        return localStorage.removeItem(key);
    }
});




//controllerModel
Model.include({
    controllers: {},
    //controller
    registerController(name, containerName) {
        return this.controllers[name] || (this.controllers[name] = new Controller(name, containerName,this));
    }
})


let totalModel = Model.create();


export {totalModel}


